import React from "react";
import MailchimpSubscribe from "react-mailchimp-subscribe";

function Content() {
  const CustomForm = ({ status, message, onValidated }) => {
    let email, name;
    const submit = () =>
      email &&
      email.value.indexOf("@") > -1 &&
      onValidated({
        EMAIL: email.value,
        tags: "4339278",
      });

    return (
      <div
        style={{
          background: "#efefef",
          borderRadius: 5,
          padding: 10,
          display: "inline-block",
        }}
      >
        <input
          style={{ fontSize: "1em", padding: 5 }}
          ref={(node) => (email = node)}
          type="email"
          placeholder="Your email"
        />
        <button style={{ fontSize: "1em", padding: 5 }} onClick={submit}>
          Submit
        </button>
        {status === "sending" && (
          <div style={{ color: "blue" }}>sending...</div>
        )}
        {status === "error" && (
          <div
            style={{ color: "red" }}
            dangerouslySetInnerHTML={{ __html: message }}
          />
        )}
        {status === "success" && (
          <div
            style={{ color: "green" }}
            dangerouslySetInnerHTML={{ __html: message }}
          />
        )}
        {/* <input
          style={{ fontSize: "2em", padding: 5 }}
          ref={(node) => (name = node)}
          type="text"
          placeholder="Your name"
        />
        <br /> */}
      </div>
    );
  };
  return (
    <div>
      <div className="container content">
        <div className="row">
          <div className="col-md-6 talk">
            <h1>Unlock Your Child's Inner Innovator</h1>
            <br />
            <h4 className="bold-four">
              Join Our Exciting Course in Creating and Launching Amazing
              Products!
            </h4>
            <br />
            <h6>
              {/* <a className="btn btn-dark start start-two lg" href="#">
                Early Bird Signup
              </a> */}
              <h2>Reserve Your Spot</h2>
              <MailchimpSubscribe
                url={process.env.REACT_APP_MAILCHIMP_KEY}
                render={({ subscribe, status, message }) => (
                  <CustomForm
                    status={status}
                    message={message}
                    onValidated={(formData) => subscribe(formData)}
                  />
                )}
              />
            </h6>
          </div>
          <div className="col-md-6 showcase-img">
            {/* <div className="circle"></div> */}
          </div>
        </div>
      </div>

      <section class="features-icons bg-light text-center det-ails">
        <div class="container">
          <div class="row">
            <div class="col-lg-4">
              <div class="features-icons-item mx-auto mb-5 mb-lg-0 mb-lg-3">
                <div class="features-icons-icon d-flex  icon-bra-ails">
                  <i class="icon-screen-desktop m-auto text-primary icon-ails"></i>
                </div>
                <h5>Lorem Tag</h5>
                <p class="lead mb-0">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                </p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="features-icons-item mx-auto mb-5 mb-lg-0 mb-lg-3">
                <div class="features-icons-icon d-flex  icon-bra-ails">
                  <i class="icon-layers m-auto text-primary icon-ails"></i>
                </div>
                <h5>Morem Tag</h5>
                <p class="lead mb-0">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                </p>
              </div>
            </div>
            <div class="col-lg-4">
              <div class="features-icons-item mx-auto mb-0 mb-lg-3">
                <div class="features-icons-icon d-flex  icon-bra-ails">
                  <i class="icon-check m-auto text-primary icon-ails"></i>
                </div>
                <h5>Oorem Tag</h5>
                <p class="lead mb-0">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section>
        <div class="container">
          <div class="row">
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                width: "50%",
              }}
            >
              <h2 id="getting-started">Sign Up</h2>
              <div style={{ backgroundColor: "lightgray" }}>
                <MailchimpSubscribe
                  url={process.env.REACT_APP_MAILCHIMP_KEY}
                  render={({ subscribe, status, message }) => (
                    <CustomForm
                      status={status}
                      message={message}
                      onValidated={(formData) => subscribe(formData)}
                    />
                  )}
                />
              </div>
            </div>
          </div>
          <hr />
        </div>
      </section>
    </div>
  );
}

export default Content;
